import {Message} from 'element-ui';
import router from '../../router'

const state = {
  loginAccount: {
    isRememberMe: false,
    password:null,
    username: null
  },
  curUser: {'username': "Ike", 'headPortrait':""},
  logining: false,
  isAutoLogin: false,
}
const getters = {
  getLoginAccount: state => { return state.loginAccount },
  getCurUser: state => { return state.curUser },
  isAutoLogin: state => {return state.isAutoLogin },
  isLogining:state => {return state.logining }
}

const mutations = {
  updateLoginAccount: (state, account) => { state.loginAccount = account },
  updateAutoLogin: (state, autoLogin ) => { state.isAutoLogin = autoLogin},
  updateLogining: (state, isLogining) => { state.logining = isLogining},
  updateCurUser:(state, user) => { state.curUser = user },

  loginSuccess: function (state, result) {
    state.curUser = result
    router.push('newOrder');
  },

  errorMsg: (state, result) => {
    if (result.code === 'current_user_not_exist') {
      router.push('login');
      return;
    }else if(result.code==='un_authorized_exception'){
      // router.push('/unAuthorized');
      return;
    }
    Message({
      message: result.message,
      type: 'error'
    });
  }
}

const actions = {
  login:(context, data) => {
    context.commit("updateLogining", true)
    Message({
      message: '登录成功',
      type: 'success'
    })
    context.commit("updateLoginAccount", data)
    context.commit("updateLogining", false)
    context.commit("loginSuccess")
    context.commit("updateCurUser",{'headPortrait':'', 'name':data.username})
  },

  logout:(context) => {
    router.push('login');
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}





