const state = {
  mMembers:[],
  memberListLoading: false,
  addMemberLoading: false,
  editMemberLoading: false,
}

const getters = {
  getMembers: (state) => state.mMembers,
  memberListLoading(){
    return state.memberListLoading;
  },

  addMemberLoading(){
    return state.addMemberLoading;
  },

  editMemberLoading(){
    return state.editMemberLoading
  },
}

const mutations = {
  updateMembers: (state,members) => state.mMembers = members,
  addMember: (state, member) => state.mMembers.push(member),
  removeMember: (state, member) => state.mMembers.splice(state.mMembers.indexOf(member),1)
}

const actions = {
  loadMembers:(context, companyId) => context.commit("updateMembers", [
    {"no":1001, "portrait":"", "name":"管理员", "department":"总部", "birthday":1538139928281, "joinDay":1538139928281, "wechatId":"", "phone":"13480540201",
      "male":1, "houseHold":"广西", "address":"", "comments":"", "workType": 0, "cardNo": "10205"},
    {"no":1002, "portrait":"", "name":"前台", "department":"总部", "birthday":1538139928281, "joinDay":1538139928281, "wechatId":"", "phone":"15780204059",
      "male":1, "houseHold":"广西", "address":"", "comments":"", "workType": 2, "cardNo": "10204"},
    {"no":1003, "portrait": "", "name":"维修工", "department":"总部", "birthday":1538139928281, "joinDay":1538139928281, "wechatId":"", "phone":"15459690031",
      "male":1, "houseHold":"广西", "address":"", "comments":"", "workType": 1, "cardNo": "10204"},
    {"no":1005, "portrait":"","name":"业务员", "department":"总部", "birthday":1538139928281, "joinDay":1538139928281, "wechatId":"", "phone":"13690302914",
      "male":1, "houseHold":"广西", "address":"", "comments":"", "workType": 1, "cardNo": "10203"}
    ]),
  removeMember:(context, member) => context.commit("removeMember", member),
  addMember:(context, member) => context.commit("addMember", member)
}

export default {
  state,
  mutations,
  getters,
  actions
}



